
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'Cronometro',
  props: {
    tempoEmSegundo: {
        type: Number,
        default: 0
    }
  },
    computed:{
tempoDecorrido () :string {
return new Date(this.tempoEmSegundo * 1000).toISOString().substring(19, 11);
}
  },
 
})
